import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve } from '@angular/router';
import * as moment from 'moment';
import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, map } from 'rxjs/operators';
import { AuthorizationService } from '../../core/services/authorization.service';
import { CompanyManagerService } from '../../core/services/company-manager.service';
import { SecurityService } from '../services/security.service';

@Injectable()
export class CompanyDateResolver // to be refactored as CompanyManagement Resolver
    implements Resolve<moment.Moment> {
    constructor(
        private companyManager: CompanyManagerService,
        private securityService: SecurityService,
        private authorizationService: AuthorizationService,
    ) { }

    resolve(
        route: ActivatedRouteSnapshot,
    ): Observable<moment.Moment> {
        return this.securityService.isSecurityReady().pipe(
            concatMap(() => {
                const company = route.params['company'];
                this.companyManager.refreshCurrentCompany(company);
                return this.companyManager.refreshCompanyDate(company);
            }),
            concatMap((companyDate: moment.Moment) => {
                // will be refactored when adding a new UserIdentityManagement Service
                return combineLatest(of(companyDate),
                                     this.authorizationService.loadCompanyPreferences(),
                );
            }),
            map(([companyDate, userCompanyPreferences]) => {
                const company = route.params['company'];
                const userPreferences = (userCompanyPreferences && userCompanyPreferences.length > 0) ? userCompanyPreferences[0] : null;
                this.authorizationService.getCurrentUser().companyPreferences[company] = userPreferences;
                this.authorizationService.user.companyPreferences[company] = userPreferences;
                return companyDate;
                // -- refactoring end
            }),
        );

    }
}
