import { Injectable } from '@angular/core';
import * as agGrid from 'ag-grid-community';
import { AtlasAgGridParam } from '../entities/atlas-ag-grid-param.entity';

@Injectable({
    providedIn: 'root',
})

export class AgGridService {

    atlasAgGridParam: AtlasAgGridParam;

    constructor() {
        this.atlasAgGridParam = new AtlasAgGridParam();
        this.atlasAgGridParam.rowHeight = 24;
        this.atlasAgGridParam.headerHeight = 48;
    }

    public getAgGridParam(): AtlasAgGridParam {
        return this.atlasAgGridParam;
    }

    public getGridDefaultRowHeight(): number {
        return this.atlasAgGridParam.rowHeight;
    }

    public sizeColumns(params: agGrid.GridOptions) {
        if (params) {
            if (params.columnApi) {
                params.columnApi.autoSizeAllColumns(); // to assure that all columns have a width before checking condition
            }

            if (params.api && params.api['gridPanel'] && params.api['gridPanel']['columnController']) {

                const availableWidth = params.api['gridPanel'].getWidthForSizeColsToFit();
                const columns = params.api['gridPanel']['columnController'].getAllDisplayedColumns();
                const usedWidth = params.api['gridPanel']['columnController'].getWidthOfColsInList(columns);

                if (params.api && usedWidth <= availableWidth) {
                    params.api.sizeColumnsToFit();
                }
            }
        }
    }
}
